import React from 'react'
import Theme from '../../../util/Theme';
import { ClipLoader } from 'react-spinners';

let MainButton = ({ buttonText, buttonDisabled, buttonType, buttonClick, buttonClass , loading }) => {
    return (
        <>
            <style>
                .btn-primary {`{
                    color: ${Theme.colors.mainColor};
                    border: 1px solid ${Theme.colors.secondaryColor};
                    padding: 10px 10px;
                    border-radius: 5px;
                    background: transparent;
                    cursor: pointer;
                    transition:.3s;
                    font-size: 20px;
                }`}
                .btn-primary:hover{`{
                    background: ${Theme.colors.mainColor};
                    color: #fff;
                    border: 1px solid ${Theme.colors.mainColor};
                }`}
                .loader{`{
                    border-color: ${Theme.colors.mainColor} ${Theme.colors.mainColor} transparent !important;
                }`}
                .btn-primary:hover .loader{`{
                    border-color: #fff #fff transparent !important;
                }`}
            </style>
            <button className={buttonClass + " btn-primary my-3"} disabled={buttonDisabled} id='main' type={buttonType} title={buttonText} onClick={buttonClick} >{loading?<ClipLoader className='loader' size={20}  />:buttonText}</button>
        </>
    )
};

export default MainButton