import { createSlice } from "@reduxjs/toolkit";
import { fetchWorksCount } from "../services/worksCounts.service";

const initialState = {
  worksCount: [],
  worksCountLoading: false,
  worksCountError: null,
};

const worksCountSlice = createSlice({
  name: "worksCount",
  initialState,
  reducers: {},
  extraReducers: {
    //fetch worksCount
    [fetchWorksCount.pending]: (state) => {
      state.worksCountLoading = true;
      state.worksCountError = null;
    },
    [fetchWorksCount.fulfilled]: (state, { payload }) => {
      state.worksCountLoading = false;
      state.worksCountError = null;
      state.worksCount = payload;
    },
    [fetchWorksCount.rejected]: (state, { error }) => {
      state.worksCountLoading = false;
      state.worksCountError = error.name;
    },
  },
});

export default worksCountSlice.reducer;
