import { createSlice } from "@reduxjs/toolkit";
import { fetchTrainers } from "../services/trainers.service";

const initialState = {
  trainers: [],
  trainersLoading: false,
  trainersError: null,
};

const trainersSlice = createSlice({
  name: "trainers",
  initialState,
  reducers: {},
  extraReducers: {
    //fetch trainers
    [fetchTrainers.pending]: (state) => {
      state.trainersLoading = true;
      state.trainersError = null;
    },
    [fetchTrainers.fulfilled]: (state, { payload }) => {
      state.trainersLoading = false;
      state.trainersError = null;
      state.trainers = payload;
    },
    [fetchTrainers.rejected]: (state, { error }) => {
      state.trainersLoading = false;
      state.trainersError = error.name;
    },
  },
});

export default trainersSlice.reducer;
