import React, { Suspense } from "react";
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import { Provider } from "react-redux";
import store from "./store/store";
import SiteLayout from "./pages/Layouts/SiteLayout";
import ErrorPage from "./pages/Errors/ErrorPage";
import Home from "./pages/Site/Home";
import ProjectGallery from "./pages/Site/ProjectGallery";
import FullScreenLazyLoading from "./components/shared/Loading/FullScreenLazyLoading";
const AuthenticatedCertificates = React.lazy(() =>import("./pages/Site/AuthenticatedCertificates"));
const AboutUs = React.lazy(() => import("./pages/Site/AboutUs"));
const ContactUs = React.lazy(() => import("./pages/Site/ContactUs"));
const PrivacyPolicy = React.lazy(() => import("./pages/Site/PrivacyPolicy"));
const CertificateView = React.lazy(() =>import("./pages/Site/certificateView/Certificateview"));
const ProjectDetails = React.lazy(() => import("./pages/Site/ProjectDetails"));
const Courses = React.lazy(() => import("./pages/Site/Courses"));
const Trainers = React.lazy(() => import("./pages/Site/Trainers"));

function Router() {
  const router = createBrowserRouter([
    {
      path: "/",
      element: <SiteLayout />,
      errorElement: <ErrorPage />,
      children: [
        { index: true, element: <Home /> },
        { path: "projectsGallery", element: <ProjectGallery /> },
        {
          path: "courses",
          element: (
            <Suspense fallback={<FullScreenLazyLoading />}>
              <Courses />
            </Suspense>
          ),
        },
        {
          path: "trainers",
          element: (
            <Suspense fallback={<FullScreenLazyLoading />}>
              <Trainers />
            </Suspense>
          ),
        },
        {
          path: "aboutUs",
          element: (
            <Suspense fallback={<FullScreenLazyLoading />}>
              <AboutUs />
            </Suspense>
          ),
        },
        {
          path: "contactUs",
          element: (
            <Suspense fallback={<FullScreenLazyLoading />}>
              <ContactUs />
            </Suspense>
          ),
        },
        {
          path: "authenticatedCertificates",
          element: (
            <Suspense fallback={<FullScreenLazyLoading />}>
              <AuthenticatedCertificates />
            </Suspense>
          ),
        },
        {
          path: "privacyPolicy",
          element: (
            <Suspense fallback={<FullScreenLazyLoading />}>
              <PrivacyPolicy />
            </Suspense>
          ),
        },
        {
          path: "project_detail/:id",
          element: (
            <Suspense fallback={<FullScreenLazyLoading />}>
              <ProjectDetails />
            </Suspense>
          ),
        },
      ],
    },
    {
      path: "certificateView/:code",
      element: (
        <Suspense fallback={<FullScreenLazyLoading />}>
          <CertificateView />
        </Suspense>
      ),
    },
  ]);

  return (
    <Provider store={store}>
      <RouterProvider router={router} />
    </Provider>
  );
}

export default Router;
