import React, { useEffect } from "react";
import { Col, Container, Row } from "react-bootstrap";
import SectionTitle from "../../components/site/SectionTitle";
import { useDispatch, useSelector } from "react-redux";
import { fetchProject, fetchProjects } from "../../services/projects.service";
import { API_LINK_STORAGE } from "../../services/ApiService";
import FullScreenLoading from "../../components/shared/Loading/FullScreenLoading";
import { useNavigate } from "react-router-dom";

const ProjectGallery = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { projects, projectsLoading, projectsError } = useSelector(
    (state) => state.projects
  );

  useEffect(() => {
    dispatch(fetchProjects());
  }, [dispatch]);

  return (
    <FullScreenLoading loading={projectsLoading} error={projectsError}>
      <section id="projectGallerySection" className="mt-5">
        <Container>
          <SectionTitle title="معرض الأعمال" />
          <Row>
            {projects.map((item,idx) => {
              return (
                <Col lg={4} md={6} sm={12} key={idx} data-aos="zoom-in">
                  <div className="item" 
                  onClick={()=>{navigate(`/project_detail/${item.id}`)}}
                  >
                    <div className="item-img">
                      <img
                        src={API_LINK_STORAGE + item.logo}
                        alt="project"
                      />
                    </div>
                    <div className="item-content">
                      <i className="fa fa-eye"></i>
                      <span className="demo">تفاصيل</span>
                    </div>
                  </div>
                </Col>
              );
            })}
          </Row>
        </Container>
      </section>
    </FullScreenLoading>
  );
};

export default ProjectGallery;
