import { createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';
import { API_LINK } from "./ApiService";

export const fetchProjects = createAsyncThunk(
    'projects/getAll',
    async (_, { rejectedWithValue }) => {
        try {
            const { data } = await axios.get(`${API_LINK}projects/getAll`)
            return data.data;
        } catch (error) {
            return rejectedWithValue(error.message)
        }
    }
)

export const fetchLastSixProjects = createAsyncThunk(
    'projects/getLastProjects',
    async (_, { rejectedWithValue }) => {
        try {
            const { data } = await axios.get(`${API_LINK}projects/getLastProjects`)
            return data.data;
        } catch (error) {
            return rejectedWithValue(error.message)
        }
    }
)

export const fetchProject = createAsyncThunk(
    'projects/get',
    async (id, { rejectedWithValue }) => {
        try {
            const { data } = await axios.get(`${API_LINK}projects/get/${id}`)
            return data.data
        } catch (error) {
            return rejectedWithValue(error.message)
        }
    }
)