import React, { useEffect } from 'react'
import AOS from 'aos';

const SectionTitle = ({ title }) => {

    useEffect(()=>{
        AOS.init({duration:500});
      },[])

    return (
        <div className="section-title" data-aos="zoom-in-up">
        <h2>{title}</h2>
        <h2>{title}</h2>
        </div>
    )
}

export default SectionTitle