import React from 'react'
import { HashLoader } from 'react-spinners'
import Theme from '../../../util/Theme'

const FullScreenLazyLoading = () => {
  return (
    <div
      style={{
        height: "100vh",
        position: "absolute",
        width: "100vw",
        top: "0",
        left: "0",
        background: "#fff",
        zIndex: "100",
      }}
      className="d-flex align-items-center justify-content-center"
    >
      <HashLoader
        loading={HashLoader}
        color={Theme.colors.mainColor}
        size={55}
      />
    </div>
  )
}

export default FullScreenLazyLoading