import { createSlice } from "@reduxjs/toolkit";
import { fetchCertificate } from "../services/certificate.service";

const initialState = {
  certificate: [],
  certificateLoading: false,
  certificateError: null,
};

const certificateSlice = createSlice({
  name: "certificate",
  initialState,
  reducers: {},
  extraReducers: {
    //fetch certificate
    [fetchCertificate.pending]: (state) => {
      state.certificateLoading = true;
      state.certificateError = null;
    },
    [fetchCertificate.fulfilled]: (state, { payload }) => {
      state.certificateLoading = false;
      state.certificateError = null;
      state.certificate = payload;
    },
    [fetchCertificate.rejected]: (state, { error }) => {
      state.certificateLoading = false;
      state.certificateError = error;
    },
  },
});

export default certificateSlice.reducer;
