import React, { useEffect, useState } from "react";
import { Col, Container, Modal, Row } from "react-bootstrap";
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Autoplay, Navigation } from "swiper";
import { useDispatch, useSelector } from "react-redux";
import CountUp from "react-countup";
import AOS from "aos";
import MainButton from "../../components/shared/Buttons/MainButton";
import SecondaryButton from "../../components/shared/Buttons/SecondaryButton";
import proServImg from "../../Assets/imgs/services/programmingIcon.svg";
import traServImg from "../../Assets/imgs/services/trainingIcon.svg";
import jobSerImg from "../../Assets/imgs/services/marketingIcon.svg";
import marSerImg from "../../Assets/imgs/services/jobIcon.svg";
import accSerImg from "../../Assets/imgs/services/accountingIcon.svg";
import WorkProcedure from "../../Assets/imgs/backgrounds/workProcedure.png";
import Bg_header from "../../Assets/imgs/backgrounds/header_bg.svg";
import Bg_header1 from "../../Assets/imgs/backgrounds/header_bg1.svg";
import Bg_header2 from "../../Assets/imgs/backgrounds/header_bg2.svg";
import Bg_header3 from "../../Assets/imgs/backgrounds/header_bg3.svg";
import headerAccImg from "../../Assets/imgs/header/accounting.svg";
import headerProImg from "../../Assets/imgs/header/programming.svg";
import headerMarImg from "../../Assets/imgs/header/marketing.svg";
import headerTraImg from "../../Assets/imgs/header/training.svg";
import avatarProfileImg from "../../Assets/imgs/avatar-profile-picture.jpg";
import SectionTitle from "../../components/site/SectionTitle";
import { fetchEmployees } from "../../services/employees.service";
import { API_LINK_STORAGE } from "../../services/ApiService";
import { fetchLastSixProjects } from "../../services/projects.service";
import { fetchCompanies } from "../../services/companies.service";
import { fetchRates } from "../../services/rates.service";
import FullScreenLoading from "../../components/shared/Loading/FullScreenLoading";
import { Link, useNavigate } from "react-router-dom";
import { fetchWorksCount } from "../../services/worksCounts.service";
import { fetchSetting } from "../../services/setting.service";

const Home = () => {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { employees, employeesLoading, employeesError } = useSelector(
    (state) => state.employees
  );
  const { projects, projectsLoading, projectsError } = useSelector(
    (state) => state.projects
  );
  const { rates, ratesLoading, ratesError } = useSelector(
    (state) => state.rates
  );
  const { companies, companiesLoading, companiesError } = useSelector(
    (state) => state.companies
  );
  const { worksCount, worksCountLoading, worksCountError } = useSelector(
    (state) => state.worksCount
  );
  const { setting, settingLoading, settingError } = useSelector(
    (state) => state.setting
  );
  const { cources, courcesLoading, courcesError } = useSelector(
    (state) => state.setting
  );

  useEffect(() => {
    AOS.init({ duration: 1000 });
  }, []);

  useEffect(() => {
    dispatch(fetchEmployees());
    dispatch(fetchLastSixProjects());
    dispatch(fetchCompanies());
    dispatch(fetchRates());
    dispatch(fetchWorksCount());
    dispatch(fetchSetting());
  }, [dispatch]);


  const worksArray = [
    {
      id: "1",
      numColor: "#b05f92",
      icon: "fa fa-fax",
      iconColor: "#157c79",
      header: "ندوات",
      headerColor: "#4f083f",
      number: worksCount.semenars,
    },
    {
      id: "2",
      numColor: "#5fb083",
      icon: "fa fa-chalkboard-user",
      iconColor: "#081c4f",
      header: "دورات",
      headerColor: "#084f3c",
      number: worksCount.courses,
    },
    {
      id: "3",
      numColor: "#5fa5b0",
      icon: "fa fa-line-chart",
      iconColor: "#4f083f",
      header: "مشاريع",
      headerColor: "#157c79",
      number: worksCount.projects,
    },
    {
      id: "4",
      numColor: "#b584ed",
      icon: "fa fa-paperclip",
      iconColor: "#084f3c",
      header: "منتجات",
      headerColor: "#081c4f",
      number: worksCount.products,
    },
  ];
  const servicesArray = [
    {
      id: "1",
      icon: proServImg,
      header: "البرمجة",
      circleColor: "#ff00e433",
      paragraph:
        "نقدم خدمات متميزة في مجال تطوير البرمجيات وتطبيقات الويب وتصميم مواقع الويب كما نوفر حلول تقنية لقطاعات أعمال مختلفة تتميز بواجهة مرنة وسهلة الاستخدام.",
      link: "",
    },
    {
      id: "2",
      icon: traServImg,
      header: "التدريب",
      circleColor: "#00f0ff33",
      paragraph:
        "نركز على تطوير المعارف والمهارات وتعزيز الفرص الوظيفية لدى الأشخاص من خلال مدربين معتمدين وأكفاء وكورسات تدريبية بمختلف المجالات التقنية والإدارية والمهنية بما يتلاءم مع تسارع الإيقاع في عصرنا الحديث.",
      link: "/Cources",
    },
    {
      id: "3",
      icon: marSerImg,
      header: "التوظيف",
      circleColor: "#84ff0044",
      paragraph:
        "تمكن خدمة التوظيف لدينا الشركات من نشر وظائفها للجمهور وجذب أفضل المواهب للشواغر المتوفرة حيث نسهل عملية التواصل بين أصحاب الأعمال والمرشحين للوظائف ونقوم بتقييم مهاراتهم وخبراتهم.",
      link: "",
    },
    {
      id: "4",
      icon: jobSerImg,
      header: "التسويق و التصميم",
      circleColor: "#fcff0055",
      paragraph:
        "نقدم كافة خدمات التصميم الجرافيكي والتصوير والتسويق حيث يملك قسم التسويق خبرات واسعة في وضع استراتيجيات ممنهجة للتسويق الإلكتروني موضوعة بطريقة احترافية لإبراز العلامات التجارية والعالمية للعملاء لبناء علاقات قوية مع الجمهور المستهدف.",
      link: "",
    },
    {
      id: "5",
      icon: accSerImg,
      header: "المحاسبة",
      circleColor: "#ff000033",
      paragraph:
        "نختص في تقديم الخدمات المحاسبية والاستشارية والمالية من خلال فريق وكادر على مستوى عالي من الاحترافية لتلبية احتياجات مختلف الشركات.",
      link: "",
    },
  ];

  return (
    <FullScreenLoading
      loading={
        employeesLoading ||
        projectsLoading ||
        ratesLoading ||
        companiesLoading ||
        worksCountLoading ||
        settingLoading
      }
      error={
        employeesError ||
        projectsError ||
        ratesError ||
        companiesError ||
        worksCountError ||
        settingError
      }
    >
      <>
        {/* header  */}
        <header>
          <Swiper
            // loop={true}
            autoplay={{
              delay: 2500,
            }}
            modules={[Autoplay]}
            className="mySwiper"
          >
            <SwiperSlide>
              <div
                className="slide-background"
                style={{
                  backgroundImage: `url(${Bg_header})`,
                  backgroundSize: "cover",
                }}
              ></div>
              <Container>
                <div className="header-slide">
                  <div className="header-slide-img">
                    <img src={headerAccImg} alt="header-img" />
                  </div>
                  <div className="header-slide-content">
                    <h2 className="header-slide-title">المحاسبة </h2>
                    <p className="header-slide-description">
                      تقديم الخدمات المحاسبية والاستشارية والمالية
                      <br />
                      فريق وكادر على مستوى عالي من الاحترافية لتلبية احتياجات
                      مختلف الشركات.
                    </p>
                    <SecondaryButton
                      buttonText={
                        <>
                          اعرف أكثر <i className="fa fa-arrow-left"></i>
                        </>
                      }
                    />
                  </div>
                </div>
              </Container>
            </SwiperSlide>
            <SwiperSlide>
              <div
                className="slide-background"
                style={{
                  backgroundImage: `url(${Bg_header1})`,
                  backgroundSize: "cover",
                }}
              ></div>
              <Container>
                <div className="header-slide">
                  <div className="header-slide-img">
                    <img src={headerProImg} alt="header-img" />
                  </div>
                  <div className="header-slide-content">
                    <h2 className="header-slide-title">البرمجة</h2>
                    <p className="header-slide-description">
                      تطوير البرمجيات وتطبيقات الويب وتصميم مواقع الويب
                      <br />
                      نوفر حلول تقنية لقطاعات أعمال مختلفة تتميز بواجهة مرنة
                      وسهلة الاستخدام.
                    </p>
                    <SecondaryButton
                      buttonText={
                        <>
                          اعرف أكثر <i className="fa fa-arrow-left"></i>
                        </>
                      }
                    />
                  </div>
                </div>
              </Container>
            </SwiperSlide>
            <SwiperSlide>
              <div
                className="slide-background"
                style={{
                  backgroundImage: `url(${Bg_header2})`,
                  backgroundSize: "cover",
                }}
              ></div>
              <Container>
                <div className="header-slide">
                  <div className="header-slide-img">
                    <img src={headerTraImg} alt="header-img" />
                  </div>
                  <div className="header-slide-content">
                    <h2 className="header-slide-title">التدريب</h2>
                    <p className="header-slide-description">
                      تطوير المعارف والمهارات وتعزيز الفرص الوظيفية
                      <br />
                      من خلال مدربين معتمدين وأكفاء وكورسات تدريبية بمختلف
                      المجالات.
                    </p>
                    <SecondaryButton
                      buttonText={
                        <>
                          اعرف أكثر <i className="fa fa-arrow-left"></i>
                        </>
                      }
                    />
                  </div>
                </div>
              </Container>
            </SwiperSlide>
            <SwiperSlide>
              <div
                className="slide-background"
                style={{
                  backgroundImage: `url(${Bg_header3})`,
                  backgroundSize: "cover",
                }}
              ></div>
              <Container>
                <div className="header-slide">
                  <div className="header-slide-img">
                    <img src={headerMarImg} alt="header-img" />
                  </div>
                  <div className="header-slide-content">
                    <h2 className="header-slide-title">التسويق والتصميم</h2>
                    <p className="header-slide-description">
                      خدمات التصميم الجرافيكي والتصوير والتسويق
                      <br />
                      خبرات واسعة في وضع استراتيجيات ممنهجة للتسويق الإلكتروني .
                    </p>
                    <SecondaryButton
                      buttonText={
                        <>
                          اعرف أكثر <i className="fa fa-arrow-left"></i>
                        </>
                      }
                    />
                  </div>
                </div>
              </Container>
            </SwiperSlide>
          </Swiper>
        </header>
        {/* end header  */}

        {/* servieces section */}
        <section id="servicesSection">
          <Container>
            <SectionTitle title="خدماتنا" />
            <h6 className="section-description">
              شركة رائدة في مجال الخدمات المحاسبية والمعلوماتية والإدارية .
            </h6>
            <div className="services">
              {servicesArray.map((item, index) => (
                <div className="item m-2" data-aos="flip-down" key={index}>
                  <div className="item-icon">
                    <img src={item.icon} width="80px" height="80px" alt="" />
                    <div className="icon-circle-path">
                      <div
                        className="icon-circle"
                        style={{ backgroundColor: item.circleColor }}
                      ></div>
                    </div>
                  </div>
                  <div className="item-content">
                    <h3 className="title">{item.header}</h3>
                    <p className="description">{item.paragraph}</p>
                    <Link to="/courses" className="link">
                      <i className="fa fa-long-arrow-left"></i>
                      "المزيد"
                    </Link>
                  </div>
                </div>
              ))}
            </div>
          </Container>
        </section>
        {/* end servieces section */}

        {/* project gallery section */}
        {projects.length === 0 ? (
          ""
        ) : (
          <section id="projectGallerySection">
            <Container>
              <SectionTitle title="معرض الأعمال" />
              <h6 className="section-description" data-aos="zoom-in-down">
                أهم الأعمال المقدمة من شركتنا
              </h6>
              <Row className="justify-content-around align-items-center">
                {projects.map((item, idx) => {
                  return (
                    <Col lg={4} md={6} sm={12} key={idx} data-aos="zoom-in">
                      <div
                        className="item"
                        onClick={() => {
                          navigate(`/project_detail/${item.id}`);
                        }}
                      >
                        <div className="item-img">
                          <img
                            src={API_LINK_STORAGE + item.logo}
                            alt="project"
                          />
                        </div>
                        <div className="item-content">
                          <i className="fa fa-eye"></i>
                          <span className="demo">تفاصيل</span>
                        </div>
                      </div>
                    </Col>
                  );
                })}
              </Row>
              <MainButton
                buttonText={
                  <Link to="/projectsGallery">
                    عرض المزيد <i className="fa fa-arrow-left"></i>
                  </Link>
                }
              />
            </Container>
          </section>
        )}
        {/* end project gallery section */}

        {/* works section */}
        <section id="worksSection">
          <Container>
            <SectionTitle title="أعمالنا" />
            <h6 className="section-description" data-aos="zoom-in-down">
              نقف إلى جانب الشركات التي هي بحاجة للدعم والتركيز على الطرق التي
              يمكن من خلالها تحسين الأعمال لخلق قيمة أكبر للعملاء.
            </h6>
            <Row className="justify-content-between align-items-center">
              {worksCount.length !== 0 &&
              worksArray.map((item, index) => {
                return index % 2 !== 0 ? (
                  <Col lg={3} md={6} sm={12} key={index} data-aos="fade-down">
                    <div className="work-item odd m-2">
                      <CountUp
                        start={0}
                        end={item.number}
                        delay={0}
                        enableScrollSpy
                      >
                        {({ countUpRef }) => (
                          <div>
                            <span
                              className="item-count"
                              style={{ color: item.numColor }}
                              ref={countUpRef}
                            >
                              {item.number}
                            </span>
                          </div>
                        )}
                      </CountUp>
                      <div className="icon">
                        <i
                          className={item.icon}
                          style={{ color: item.iconColor }}
                        ></i>
                      </div>
                      <h2
                        className="item-title"
                        style={{ color: item.headerColor }}
                      >
                        {item.header}
                      </h2>
                    </div>
                  </Col>
                ) : (
                  <Col lg={3} md={6} sm={12} key={index} data-aos="fade-down">
                    <div className="work-item even m-2">
                      <CountUp
                        start={0}
                        end={item.number}
                        delay={0}
                        enableScrollSpy
                      >
                        {({ countUpRef }) => (
                          <div>
                            <span
                              className="item-count"
                              style={{ color: item.numColor }}
                              ref={countUpRef}
                            >
                              {item.number}
                            </span>
                          </div>
                        )}
                      </CountUp>
                      <div className="icon">
                        <i
                          className={item.icon}
                          style={{ color: item.iconColor }}
                        ></i>
                      </div>
                      <h2
                        className="item-title"
                        style={{ color: item.headerColor }}
                      >
                        {item.header}
                      </h2>
                    </div>
                  </Col>
                );
              })}
            </Row>
          </Container>
        </section>
        {/* end works section */}

        {/* work procedure section  */}
        <section id="workProcedureSection">
          <Container>
            <SectionTitle title="خطوات العمل" />
            <div className="section-items">
              <img
                src={WorkProcedure}
                className="section-bg"
                data-aos="flip-up"
              ></img>
              <div className="work-procedure-contents">
                <div className="item item1" data-aos="flip-right">
                  <div className="item-shape">
                    <div className="item-id">1</div>
                    <div className="circle">
                      <div className="back-circle"></div>
                      <div className="front-circle"></div>
                    </div>
                    <i className="fa-regular fa-lightbulb"></i>
                  </div>
                  <div className="item-content">
                    <h3 className="title">التعريف</h3>
                    <p className="description">
                      أولاً ، نأخذ وقتًا لفهم عملك واحتياجاتك ومشاكلك التي يجب
                      معالجتها.
                    </p>
                  </div>
                </div>
                <div className="item item2" data-aos="flip-left">
                  <div className="item-shape">
                    <div className="item-id">2</div>
                    <div className="circle">
                      <div className="back-circle"></div>
                      <div className="front-circle"></div>
                    </div>
                    <i className="fa-solid fa-laptop-code"></i>
                  </div>
                  <div className="item-content">
                    <h3 className="title">التصميم</h3>
                    <p className="description">
                      بعد ذلك ، نصمم حلًا محددًا بعناية ونقترحه عليك.
                    </p>
                  </div>
                </div>
                <div className="item item3" data-aos="flip-right">
                  <div className="item-shape">
                    <div className="item-id">3</div>
                    <div className="circle">
                      <div className="back-circle"></div>
                      <div className="front-circle"></div>
                    </div>
                    <i className="fa-solid fa-rocket"></i>
                  </div>
                  <div className="item-content">
                    <h3 className="title">التطوير</h3>
                    <p className="description">
                      ثم ، نتحرك نحو تطوير هذا الحل ، قد يكون تطبيقًا أو
                      برنامجًا أو موقعًا إلكترونيًا ذكيًا.
                    </p>
                  </div>
                </div>
                <div className="item item4" data-aos="flip-left">
                  <div className="item-shape">
                    <div className="item-id">4</div>
                    <div className="circle">
                      <div className="back-circle"></div>
                      <div className="front-circle"></div>
                    </div>
                    <i className="fa-solid fa-bullseye"></i>
                  </div>
                  <div className="item-content">
                    <h3 className="title">التسليم</h3>
                    <p className="description">
                      أخيرًا ، ننهي المشروع ، ونسلمك المنتج ، ونقدم لك الدعم
                      الفني المستمر.
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </Container>
        </section>
        {/* end work procedure section  */}

        {/* about section */}
        {setting.length === 0 ? (
          ""
        ) : (
          <section id="aboutSection">
            <Container>
              <div className="about-video" data-aos="zoom-in-left">
                <video src={API_LINK_STORAGE + setting} type="video/mp4" />
                <div className="overlay"></div>
                <MainButton
                  buttonClick={handleShow}
                  buttonText={
                    <>
                      <i className="fa fa-play"></i>
                    </>
                  }
                />
                <Modal show={show} onHide={handleClose} animation={false}>
                  <video
                    src={API_LINK_STORAGE + setting}
                    controls
                    width="75%"
                    const
                    type="video/mp4"
                    style={{
                      position: "fixed",
                      top: "50%",
                      left: "50%",
                      transform: "translate(-50%, -50%)",
                    }}
                  />
                </Modal>
              </div>
              <div
                id="about"
                className="about-content"
                data-aos="zoom-in-right"
              >
                <h2 className="title">من نحن</h2>
                <p className="description">
                  شركة "أسيست" تأسست عام 2022 وهي شركة رائدة في مجال الخدمات
                  المحاسبية والمعلوماتية والإدارية لكافة الشركات والمؤسسات
                  الخاصة والعامة . تقع في مدينة حمص سوريا. وتعمل مع عدة شركاء
                  داخل القطر وخارجه للارتقاء بأعمالهم إلى مستوى جديد من الابداع
                  والإنتاجية باستخدام أحدث البرتوكولات التقنية والعلمية .
                </p>
                <MainButton
                  buttonText={
                    <Link to="/aboutUs">
                      المزيد <i className="fa fa-arrow-left"></i>
                    </Link>
                  }
                />
              </div>
            </Container>
          </section>
        )}
        {/* end about section */}

        {/* Our Team Section */}
        {employees.length === 0 ? (
          ""
        ) : (
          <section id="ourTeamSection">
            <Container>
              <SectionTitle title="فريقنا" />
              <h6 className="section-description" data-aos="zoom-in-down">
                اعضاء الفريق الخاص بنا
              </h6>
              <Swiper
                spaceBetween={50}
                pagination={true}
                modules={[Pagination, Navigation]}
                breakpoints={{
                  0: {
                    slidesPerView: 1,
                  },
                  768: {
                    slidesPerView: 2,
                  },
                  1080: {
                    slidesPerView: 3,
                  },
                }}
                className="mySwiper our-team-swiper"
              >
                {employees.map((item, idx) => {
                  return (
                    <SwiperSlide key={idx}>
                      <div className="profile-card" data-aos="fade-left">
                        <div className="profile-img">
                          <img
                            src={
                              item.img_path
                                ? API_LINK_STORAGE + item.img_path
                                : avatarProfileImg
                            }
                          />
                        </div>
                        <div className="profile-content">
                          <h3 className="name">{item.name}</h3>
                          <p className="job-title">{item.job_title}</p>
                          <div className="social-accounts">
                            {item.facebook_link ? (
                              <a href={item.facebook_link}>
                                <i className="fa-brands fa-facebook-f"></i>
                              </a>
                            ) : (
                              ""
                            )}
                            {item.whatsapp_link ? (
                              <a href={item.whatsapp_link}>
                                <i className="fa-brands fa-whatsapp"></i>
                              </a>
                            ) : (
                              ""
                            )}
                            {item.telegram_link ? (
                              <a href={item.telegram_link}>
                                <i className="fa-brands fa-telegram"></i>
                              </a>
                            ) : (
                              ""
                            )}
                            {item.linkedin_link ? (
                              <a href={item.linkedin_link}>
                                <i className="fa-brands fa-linkedin"></i>
                              </a>
                            ) : (
                              ""
                            )}
                          </div>
                        </div>
                      </div>
                    </SwiperSlide>
                  );
                })}
              </Swiper>
            </Container>
          </section>
        )}
        {/* end Our Team Section */}

        {/* feedback section */}
        {rates.length === 0 ? (
          ""
        ) : (
          <section id="feedbackSection">
            <Container>
              <SectionTitle title="آراء العملاء" />
              <div className="section-description" data-aos="zoom-in-down">
                نهدف إلى مساعدة عملائنا في زيادة فعالية عملياتهم المستمرة وتوسيع
                أعمالهم وتعزيز ربحيتهم.
              </div>
              <Swiper
                slidesPerView={1}
                centeredSlides={true}
                pagination={true}
                modules={[Pagination, Navigation]}
                className="mySwiper feedback-swiper"
              >
                {rates.map((item, idx) => {
                  return (
                    <SwiperSlide key={idx}>
                      <div className="feedback-bg" data-aos="fade-right">
                        <svg
                          version="1.2"
                          xmlns="http://www.w3.org/2000/svg"
                          viewBox="0 0 852 424"
                          width="768"
                          height="380"
                          fill="#3dbfb9"
                        >
                          <path
                            id="Layer"
                            className="mid"
                            d="m817 217.5c0 89.2-319.6 199-521.5 199-201.9 0-225.5-46.3-225.5-135.5 0-89.2 100.5-294 354.5-266.5 201.9 0 392.5 92.5 392.5 203z"
                          />
                          <path
                            id="Layer"
                            className="right"
                            d="m89 98.5c10-29.2-10.5-32.2-22-30-9.3 12.2-31.5 49.3-45.5 100.5-14 51.2-5.8 93.3 0 108 1.3-2.5 5.5-13.9 11.5-39.5 7.5-32 43.5-102.5 56-139z"
                          />
                          <path
                            id="Layer"
                            className="left"
                            d="m818.2 109.2c9.6 11.5 16.3 0.1 18.5-7-2.7-7.8-12.5-27.9-30.4-45.9-18-18-37.9-21.4-45.7-20.8 0.7 1.3 4.4 5.9 13.7 14.3 11.5 10.5 31.7 45.1 43.9 59.4z"
                          />
                        </svg>
                      </div>
                      <div className="feedback-content" data-aos="fade-left">
                        <i className="fa fa-quote-right fa-4x"></i>
                        <span className="feedback-text">
                          {item.description}
                        </span>
                        <span className="feedback-rating">
                          <i className="fa fa-star"></i>
                          <i className="fa fa-star"></i>
                          <i className="fa fa-star"></i>
                          <i className="fa fa-star"></i>
                          <i className="fa fa-star"></i>
                        </span>
                        <div className="feedback-holder">
                          <div className="feedback-holder-img">
                            <img
                              src={API_LINK_STORAGE + item.img_path}
                              alt="feedback-holder-img"
                            />
                          </div>
                          <div className="feedback-holder-data">
                            {item.name}
                          </div>
                        </div>
                      </div>
                    </SwiperSlide>
                  );
                })}
              </Swiper>
            </Container>
          </section>
        )}
        {/* end feedback section */}

        {/* end cooperating companies section */}
        {companies.length === 0 ? (
          ""
        ) : (
          <section id="cooperatingCompaniesSection">
            <Container>
              <Swiper
                autoplay={{
                  delay: 1500,
                  disableOnInteraction: false,
                }}
                loop={true}
                spaceBetween={50}
                pagination={{
                  type: "progressbar",
                }}
                modules={[Autoplay, Pagination]}
                breakpoints={{
                  0: {
                    slidesPerView: 3,
                  },
                  768: {
                    slidesPerView: 4,
                  },
                  1080: {
                    slidesPerView: 5,
                  },
                }}
                className="addsSwiper"
              >
                {companies.map((item, idx) => {
                  return (
                    <SwiperSlide key={idx}>
                      <img
                        src={API_LINK_STORAGE + item.img_path}
                        alt={item.name}
                        data-aos="flip-left"
                      />
                    </SwiperSlide>
                  );
                })}
              </Swiper>
            </Container>
          </section>
        )}

        {/* end adds section */}
      </>
    </FullScreenLoading>
  );
};

export default Home;
