import React from "react";
import Theme from "../../../util/Theme";
import { ClipLoader } from "react-spinners";

let SecondaryButton = ({
  buttonText,
  buttonDisabled,
  buttonType,
  buttonClick,
  buttonClass,
  loading
}) => {
  return (
    <>
      <style>
        .btn-secondary
        {`{
                    color: ${Theme.colors.lightColor};
                    border: 1px solid ${Theme.colors.secondaryColor};
                    padding: 10px 10px;
                    border-radius: 5px;
                    background: ${Theme.colors.secondaryColor};
                    cursor: pointer;
                    transition: .5s;
                }`}
        .btn-secondary:hover
        {`{
                    background: transparent;
                    color: ${Theme.colors.secondaryColor};
                }`}
                 .loader{`{
                    border-color: ${Theme.colors.lightColor} ${Theme.colors.lightColor} transparent !important;
                    
                }`}
                .btn-secondary:hover .loader{`{
                    border-color: ${Theme.colors.secondaryColor} ${Theme.colors.secondaryColor} transparent !important;
                }`}
      </style>
      <button
        className={buttonClass + " btn-secondary my-3"}
        disabled={buttonDisabled}
        id="main"
        type={buttonType}
        title={buttonText}
        onClick={buttonClick}
      >
        {loading?<ClipLoader className='loader' size={20}  />:buttonText}
      </button>
    </>
  );
};

export default SecondaryButton;
