import { createSlice } from "@reduxjs/toolkit";
import { fetchCompanies } from "../services/companies.service";

const initialState = {
  companies: [],
  companiesLoading: false,
  companiesError: null,
};

const companiesSlice = createSlice({
  name: "companies",
  initialState,
  reducers: {},
  extraReducers: {
    //fetch companies
    [fetchCompanies.pending]: (state) => {
      state.companiesLoading = true;
      state.companiesError = null;
    },
    [fetchCompanies.fulfilled]: (state, { payload }) => {
      state.companiesLoading = false;
      state.companiesError = null;
      state.companies = payload;
    },
    [fetchCompanies.rejected]: (state, { error }) => {
      state.companiesLoading = false;
      state.companiesError = error.name;
    },
  },
});

export default companiesSlice.reducer;
