import { createSlice } from "@reduxjs/toolkit";
import { fetchSetting } from "../services/setting.service";

const initialState = {
  setting: [],
  settingLoading: false,
  settingError: null,
};

const settingSlice = createSlice({
  name: "setting",
  initialState,
  reducers: {},
  extraReducers: {
    //fetch setting
    [fetchSetting.pending]: (state) => {
      state.settingLoading = true;
      state.settingError = null;
    },
    [fetchSetting.fulfilled]: (state, { payload }) => {
      state.settingLoading = false;
      state.settingError = null;
      state.setting = payload;
    },
    [fetchSetting.rejected]: (state, { error }) => {
      state.settingLoading = false;
      state.settingError = error;
    },
  },
});

export default settingSlice.reducer;
