import { createSlice } from "@reduxjs/toolkit";
import { fetchRates } from "../services/rates.service";

const initialState = {
  rates: [],
  ratesLoading: false,
  ratesError: null,
};

const ratesSlice = createSlice({
  name: "rates",
  initialState,
  reducers: {},
  extraReducers: {
    //fetch rates
    [fetchRates.pending]: (state) => {
      state.ratesLoading = true;
      state.ratesError = null;
    },
    [fetchRates.fulfilled]: (state, { payload }) => {
      state.ratesLoading = false;
      state.ratesError = null;
      state.rates = payload;
    },
    [fetchRates.rejected]: (state, { error }) => {
      state.ratesLoading = false;
      state.ratesError = error.name;
    },
  },
});

export default ratesSlice.reducer;
