import { useState } from "react";
import { Link, Outlet } from "react-router-dom";
import { Container, Navbar, Nav, Row, Col } from "react-bootstrap";
import { ToastContainer } from "react-toastify";
import logoImg from "../../Assets/imgs/logo.png";
import FooterLogoImg from "../../Assets/imgs/logo_white.png";

const SiteLayout = () => {
  // add class to navbar on scroll to make it sticky
  window.addEventListener("scroll", () => {
    let value = window.scrollY;
    if (value > 10) {
      document.getElementById("navbar").classList.add("sticky");
    } else {
      document.getElementById("navbar").classList.remove("sticky");
    }
  });

  return (
    <>
      <ToastContainer></ToastContainer>
      <Navbar id="navbar" expand="lg">
        <Container>
          <Navbar.Brand>
            <Link to="/">
              <img src={logoImg} alt="" height="65px" />
            </Link>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarCollapse" />
          <Navbar.Collapse id="navbarCollapse">
            <Nav className="me-auto">
              <Link to="/">
                <i className="fa-solid fa-home"></i> الصفحة الرئيسية
              </Link>
              <Link to="/projectsGallery">
                <i className="fa-solid fa-line-chart"></i> الاعمال
              </Link>
              <Link to="/aboutUs">
                <i className="fa-solid fa-circle-info"></i> من نحن
              </Link>
              <Link to="/contactUs">
                <i className="fa-solid fa-phone"></i> تواصل معنا
              </Link>
              <Link to="/authenticatedCertificates">
                <i className="fa-solid fa-circle-check"></i> التحقق من الشهادة
              </Link>
              <Link to="/trainers">
              <i class="fa-solid fa-chalkboard-user"></i> المدربين
              </Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <Outlet />
      <footer>
        <Container>
          <Row>
            <Col lg={4} md={12} className="mb-4 aboutus-footer">
              <Link to="/" className="mb-3 footer-brand">
                <img src={FooterLogoImg} className="img-fluid" />
              </Link>
              <p>
                اسست هذه الشركة عام 2022 <br />
                هدفها الأول والرئيسي تأهيل الشباب ورفع مستوياتهم العلمية في كافة
                المجالات البرمجية
              </p>
            </Col>
            <Col lg={2} md={6} className="mb-4 footer-urls">
              <ul>
                <li className="mb-3">
                  <h5>اهم الروابط</h5>
                </li>
                <li className="mb-1">
                  <Link to="/">
                    <i className="fa-solid fa-home"></i> الصفحة الرئيسية
                  </Link>
                </li>
                <li className="mb-1">
                  <Link to="/projectsGallery">
                    <i className="fa-solid fa-line-chart"></i> الاعمال
                  </Link>
                </li>
                <li className="mb-1">
                  <Link to="/aboutUs">
                    <i className="fa-solid fa-circle-info"></i> من نحن
                  </Link>
                </li>
                <li className="mb-1">
                  <Link to="/contactUs">
                    <i className="fa-solid fa-phone"></i> تواصل معنا
                  </Link>
                </li>
                <li className="mb-1">
                  <Link to="/privacyPolicy">
                    <i className="fa-solid fa-key"></i> سياسة الخصوصية
                  </Link>
                </li>
              </ul>
            </Col>
            <Col lg={2} md={6} className="mb-4">
              <ul className="footer-contact">
                <li className="mb-3">
                  <h5>تواصل معنا</h5>
                </li>
                <li className="mb-1">
                  <a href="https://goo.gl/maps/cxzS1k5gSrv6Cai19">
                    <i className="fa-solid fa-location-dot"></i> حمص
                  </a>
                </li>
                <li className="mb-1">
                  <a href="tel:+963996697938">
                    <i className="fa-solid fa-phone"></i> 099723963495
                  </a>
                </li>
                <li className="mb-1">
                  <a href="mailto:assist@info.com">
                    <i className="fa-solid fa-envelope"></i> assist@info.com
                  </a>
                </li>
              </ul>
            </Col>
            <Col lg={4} md={12}>
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d356.55415704714164!2d36.71231642590482!3d34.73144971686381!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x15230f5401aecb0d%3A0xec4a5f4fd9eaf9a8!2sAssist%20Co.%20%26%20Training%20center!5e0!3m2!1sar!2snl!4v1681731317227!5m2!1sar!2snl"
                loading="lazy"
              ></iframe>
            </Col>
          </Row>
          <Row>
            <Col lg={12} className="social-media">
              <a href="https://www.facebook.com/we.assist.it">
                <i className="fa-brands fa-facebook-f"></i>
              </a>
              <a href="https://wa.me/00963940245564">
                <i className="fa-brands fa-whatsapp"></i>
              </a>
              <a href="https://t.me/username">
                <i className="fa-brands fa-telegram"></i>
              </a>
              {/*
              <a href="#!">
                <i className="fa-brands fa-linkedin"></i>
              </a>
              */}
            </Col>
          </Row>
        </Container>
        <Row>
          <Col className="copyright" lg={12}>
            © 2023 &nbsp; Copyright &nbsp;
            <Link className="company-name" to="/privacyPolicy">
              Assist
            </Link>
          </Col>
        </Row>
      </footer>
    </>
  );
};

export default SiteLayout;
