import { createSlice } from "@reduxjs/toolkit";
import { fetchEmployees } from "../services/employees.service";

const initialState = {
  employees: [],
  employeesLoading: false,
  employeesError: null,
};

const employeesSlice = createSlice({
  name: "employees",
  initialState,
  reducers: {},
  extraReducers: {
    //fetch employees
    [fetchEmployees.pending]: (state) => {
      state.employeesLoading = true;
      state.employeesError = null;
    },
    [fetchEmployees.fulfilled]: (state, { payload }) => {
      state.employeesLoading = false;
      state.employeesError = null;
      state.employees = payload;
    },
    [fetchEmployees.rejected]: (state, { error }) => {
      state.employeesLoading = false;
      state.employeesError = error.name;
    },
  },
});

export default employeesSlice.reducer;
