import { createSlice } from "@reduxjs/toolkit";
import {
  fetchLastSixProjects,
  fetchProject,
  fetchProjects,
} from "../services/projects.service";

const initialState = {
  projects: [],
  projectsLoading: false,
  projectsError: null,
  project:[]
};

const projectsSlice = createSlice({
  name: "projects",
  initialState,
  reducers: {},
  extraReducers: {
    //fetch projects
    [fetchProjects.pending]: (state) => {
      state.projectsLoading = true;
      state.projectsError = null;
    },
    [fetchProjects.fulfilled]: (state, { payload }) => {
      state.projectsLoading = false;
      state.projectsError = null;
      state.projects = payload;
    },
    [fetchProjects.rejected]: (state, { error }) => {
      state.projectsLoading = false;
      state.projectsError = error.name;
    },

    //fetch last six projects
    [fetchLastSixProjects.pending]: (state) => {
      state.projectsLoading = true;
      state.projectsError = null;
    },
    [fetchLastSixProjects.fulfilled]: (state, { payload }) => {
      state.projectsLoading = false;
      state.projectsError = null;
      state.projects = payload;
    },
    [fetchLastSixProjects.rejected]: (state, { error }) => {
      state.projectsLoading = false;
      state.projectsError = error.name;
    },

    //fetch project
    [fetchProject.pending]: (state) => {
      state.projectsLoading = true;
      state.projectsError = null;
    },
    [fetchProject.fulfilled]: (state, {payload}) => {
      state.projectsLoading = false;
      state.projectsError = null;
      state.project = payload;
    },
    [fetchProject.rejected]: (state, {error}) => {
      state.projectsLoading = false;
      state.projectsError = error.name;
    },
  },
});

export default projectsSlice.reducer;
