import { configureStore } from "@reduxjs/toolkit";
import employees from "./employeesSlice";
import projects from "./projectsSlice";
import companies from "./companiesSlice";
import rates from "./ratesSlice";
import worksCount from "./worksCountSlice";
import certificate from "./certificateSlice";
import setting from "./settingSlice";
import courses from "./coursesSlice";
import trainers from "./trainersSlice";

const store = configureStore({
  reducer: {
    employees,
    projects,
    companies,
    rates,
    worksCount,
    certificate,
    setting,
    courses,
    trainers
  },
});

export default store;
